<template>
  <v-data-table v-bind="$attrs" v-on="$listeners">
      <template v-slot:top="options">
        <slot name="top" v-bind="options"></slot>
      </template>
      
        <template v-slot:expanded-item="options">
            <slot name="expanded-item" v-bind="options"></slot>
        </template>
      <template v-slot:item="{ headers, item, isMobile, isSelected, select, isExpanded, expand }" >
                <tr :class="{'v-data-table__mobile-table-row': isMobile}" @click="$emit('click:row', item)">
                    <td 
                    v-for="header in headers" 
                    :key="header.value" 
                    :width="!isMobile ? header.width : null"
                    :class="[{'v-data-table__mobile-row': isMobile},{'v-data-table__divider': !isMobile}, {[`text-${header.align || 'start'}`]: !isMobile}, ...header.class]"
                    >
                        <div :class="{'v-data-table__mobile-row__header': isMobile}" v-if="isMobile">{{header.text}}</div>
                        
                 
                        <div v-if="header.value == 'data-table-expand'"  :class="{'v-data-table__mobile-row__cell': isMobile}" >
                          <v-btn text @click.prevent="expand(!isExpanded)"   > <v-icon>mdi-chevron-{{ !isExpanded ? 'down' : 'up'}}</v-icon>  </v-btn>
                        </div>
                        <div v-else-if="header.value == 'data-table-select'"  :class="{'v-data-table__mobile-row__cell': isMobile}" >
                            <VSimpleCheckbox :ripple="false" class="v-data-table__checkbox" :value="isSelected" @input="select" />
                        </div>
                        <div  v-else-if="header.value == 'actions'"   :class="{'v-data-table__mobile-row__cell': isMobile}" >
                            <v-tooltip bottom v-for="(btn, ind) in getHeaderButtons(header, item)" :key="ind" >
                                <template v-slot:activator="{ on, attrs }">
                                    <v-btn icon
                                        v-bind="typeof btn.bind == 'function' ? btn.bind(vm, item) : btn.bind"
                                        v-on="typeof btn.on == 'function' ? btn.on(vm, item) : btn.on"
                                    >

                                        <v-icon
                                        v-bind="attrs" 
                                        v-on="on"
                                        small
                                      
                                        >
                                        {{ btn.icon }}
                                        </v-icon>
                                    </v-btn>
                                </template>
                                <span>{{btn.title}}</span>
                            </v-tooltip>
            
                        </div>
                        <div v-else-if="header.component"  :class="{'v-data-table__mobile-row__cell': isMobile}" >
                            
                            <component 
                            :is="header.component.is || header.component" 
                            v-bind="typeof header.component == 'function' ? {} : typeof header.component.bind == 'function' ? header.component.bind(vm, item) : (header.component.bind || {})"
                            v-on="typeof header.component.on == 'function' ? header.component.on(vm, item) : header.component.on"
                            :item="item" 
                            :header="header"
                            :parent="vm"
                            ></component>

                        </div>

                        <div v-else  :class="{'v-data-table__mobile-row__cell': isMobile}"  v-html="$options.filters.dynamic([item, header.value], header.format, getFormatArgs(header.formatArgs, item) || [])"></div>

                    </td>
                </tr>
      </template>
  </v-data-table>
</template>
<script>
 

export default {
  name: 'data-table',
  props:{
      parent: Object
  },
  computed: {
    vm () {
        return this.parent ? this.parent : this.$parent
    },
  },
  methods: {
      fn (fn) {
          console.log(fn);
      },
    getHeaderButtons (header, item) {
        if (!header.buttons) return []

        return header.buttons.filter(btn => {
            if (btn.if) {
                switch (typeof btn.if) {
                    case 'function':
                    return btn.if(this.vm, item);
                    case 'boolean':
                    return btn.if;
                    default:
                    return true;
                }
            } else
            return true
        })
    }
  }
  
}
</script>