<template>
  <v-container>
    <v-layout d-flex column>
      <v-flex>
        <date-picker v-model="dates" ></date-picker>
      </v-flex>
      <v-progress-linear indeterminate v-if="loading"></v-progress-linear>
 
    <v-tabs v-model="tab" class="my-6" show-arrows>
      <v-tab v-for="(tab, ind ) in tabs" :key="ind" >
        <v-icon class="mx-1">{{tab.icon}}</v-icon>
        <span >{{tab.title}}</span>
      </v-tab>
    </v-tabs>
     <v-layout d-flex  column >
      <v-flex class="my-6 d-flex">
        
        <v-row>
          <v-col>
             <v-text-field class="my-4" outlined v-model="search"
              prepend-inner-icon="mdi-magnify" hide-details 
              :placeholder="placeholder"
              dense style="max-width: 100%"
              clearable></v-text-field>
          </v-col>
          <v-col class="d-flex flex-column align-end"   >
            <span class="caption font-weight-bold " v-if="!tab">Group By</span>
            <v-btn-toggle  v-model="toggle_by" class="transparent" v-if="!tab">
              <v-btn class="px-2" :disabled="!toggle_by">
                <v-icon class="mx-1">mdi-account-box</v-icon>
                Customer
              </v-btn>
              <v-btn class="px-2" :disabled="toggle_by == 1">
                <v-icon class="mx-1">mdi-account-network-outline</v-icon>
                Affiliate
              </v-btn>
              <v-btn class="px-2" :disabled="toggle_by == 2">
                <v-icon class="mx-1">mdi-account</v-icon>
                Owner
              </v-btn>
            </v-btn-toggle>

            <v-select v-else 
            outlined v-model="type"
              prepend-inner-icon="mdi-format-list-bulleted-type" hide-details 
              :placeholder="placeholder"
              dense style="max-width: 100%"
               class="my-4" 
            
            :items="[{ text: 'All', value: null }, { text: 'Sales', value: 'ftd' }, { text: 'Retentions', value: 'rtn' }]"></v-select>

          </v-col>
          
        </v-row>
      </v-flex>
      <!-- <v-flex cols="12" class="my-4"  >
       <data-table   :items="total.items" :headers="total.headers" hide-default-footer></data-table>
      </v-flex> -->
    </v-layout>
    <v-divider ></v-divider>


    
       
      <v-flex cols="12"  >
       <data-table 
        :items="items" 
        :headers="headers"
        :expanded.sync="expanded"
        item-key="_id"
        single-expand 
        :show-expand="showExpanded"
        > 
        <template v-slot:expanded-item="{item, headers}">
          <tr class="grey lighten-2">
            <td class="pb-6 pt-2" :colspan="headers.length">
              <v-divider></v-divider>
              <data-table   :items="table.subItems" :headers="child_headers" :loading="table.loading" :parent="vm"></data-table>
              <v-divider></v-divider>
            
            </td>
          </tr>

          
        </template>
       </data-table>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import utility from './../../utility'
import DatePicker from './../../components/dashboard/DatePicker' 
import DataTable from './../../components/DataTable'
import debounce from 'debounce'
import report_config from './config'
export default {
  components: {
    DatePicker,
    DataTable
  },
  computed: {
    vm () { return this },
    config () {
      return report_config[this.tab]
    },
    placeholder () {
      if (this.toggle_by == 0) return 'Search Customer / TP...';
      if (this.toggle_by == 1) return 'Search Affiliate...';
      if (this.toggle_by == 2) return 'Search Owner...';
      if (this.toggle_by == 3) return 'Search Payment Method...';
      if (this.toggle_by == 4) return 'Search Country...';
      return 'Search...'
    },
    active () {
      return this.tabs[this.tab]
    },
    searchFields () {

      return !this.tab ? this.config.search[this.groupBy] : this.config.search

    },
    groupBy () {
      return this.by[this.toggle_by]
    },
    headers () {
      return !this.tab ? this.config.headers[this.groupBy] : this.config.headers
    },
    items () {
      return this.loading ? [] : this.table.items
    },
    showExpanded () {
     
      return this.config.expand ? this.config.expand.indexOf(this.groupBy) > -1 : false
    },
    child_headers () {
      return this.groupBy == 'user' ? this.config.headers.user_customer : this.config.headers.customer

    }
  },
  data: () => ({
    type: null,
    expanded: [],
    dates:  utility.currentDateRange,
    toggle_by: 0,
    by: ['customer', 'affiliate', 'user', 'payment_method', 'country'],
    tab: 0,
    tabs: [
      {
        title: "Upsale Sales",
        icon: "mdi-chart-timeline-variant-shimmer",
        api: {
          items: 'deposit/upsales_sales',
          total: 'deposit/upsales_sales',
        }
      },
      {
        title: "Customer",
        icon: "mdi-account-box",
        api: {
          items: 'deposit/report',
          total: 'deposit/report',
        }

      },
      {
        title: "Affiliate",
        icon: "mdi-account-network-outline",
        api: {
          items: 'deposit/report',
          total: 'deposit/report',
        }

      },
      {
        title: "User",
        icon: "mdi-account",
        api: {
          items: 'deposit/report',
          total: 'deposit/report',
        }

      },
      {
        title: "Payment",
        icon: "mdi-credit-card",
        api: {
          items: 'deposit/report',
          total: 'deposit/report',
        }

      },
      {
        title: "Countries",
        icon: "mdi-flag",
        api: {
          items: 'deposit/report',
          total: 'deposit/report',
        }

      }
    ],
    // total:{
    //   items: [],
    //   headers: [
      
    //     {
    //       text: 'Deposits',
    //       value: 'count',
    //       class:['font-weight-bold', 'subtitle-1'],
    //       width: '120',
          
    //     },
    //     {
    //       text: 'Total',
    //       class:['font-weight-bold', 'subtitle-1'],
    //       value: 'amount',
    //       format: 'money'
    //     }
        
    //     ]

    // },
    table: {
      loading: false,
      subItems: [],
      items: [],
      headers: []
    },
    subLoading: false,
    loading: true,
    search: '',
   

  }),
  watch: {
    async expanded (ex) {
      if (!ex.length) return;
      const [ item ] = ex
      this.table.subItems = []
      switch (this.groupBy) {
        case 'affiliate':
          this.getAffiliateFtd(item)
          break;
        case 'user':
            this.getUserFtd(item)
            break;
        default:
          break;
      }
    

   
    },
    search: debounce( function () {
      this.expanded = []
      this.fetch()
    }, 500),
    dates:{
      immediate:true,
      handler:'fetch' 
    },
    type () {
      this.search = ''
      this.fetch()
    },
    tab (v) {
      this.expanded = []
      
      this.type = null
      this.toggle_by = !v ? 0 : v - 1
      this.search = ''
      this.fetch()
    },
    toggle_by () {
      this.expanded = []
      this.search = ''
      if (!this.tab)
        this.fetch()
    }
  },
  methods:{
    async getUserFtd (item) {
 

 
      const query =  { 
        created_on: this.dates.join('|'),
        group: 'user_customer',
        [this.groupBy]:  item._id
      } 
      
      
      this.table.loading = true
      const response = await this.get({ collection: this.active.api.items, 
        query
      })
      this.table.subItems = response.data
      this.table.loading = false

      
    },
    async getAffiliateFtd (item) {
        
      const grouped_by = item[this.groupBy]

 
      const query =  { 
        created_on: this.dates.join('|'),
        group: 'customer',
        [this.groupBy]:  grouped_by._id
      } 
      
      
      this.table.loading = true
      const response = await this.get({ collection: this.active.api.items, 
        query
      })

      this.table.subItems = response.data
      this.table.loading = false
    },
    async fetch () {
      this.loading = true
      if (!this.tab) {
        this.fetchUpsales()
      } else {
        this.fetchByType()
      }
      this.loading = false
        
       
    },
    async fetchByType () {
       const query =  { 
        created_on: this.dates.join('|'),
        group: this.groupBy 
      } 

      if (this.type) {
        query.type = this.type
      }
      if (this.search) {
        query['orFields'] = this.searchFields
        query['orValues'] = this.searchFields.map(() => '~' + this.search.trim())
      }
      const response = await this.get({ collection: this.active.api.items, 
        query
      })
      this.table.items = response.data  

    },
    async fetchUpsales () {
       const query =  { 
        created_on: this.dates.join('|'),
        group: this.groupBy 
      } 
      if (this.search) {
        query['orFields'] = this.searchFields
        query['orValues'] = this.searchFields.map(() => '~' + this.search.trim())
      }
 
      const response = await this.get({ collection: this.active.api.items, 
          query
        })
   
      this.table.items = response.data  
      
      
    }
  } 
}


</script>

