export default {
    search: ['customer.name', 'customer.tpc'],
    headers: [
        {
            text: 'TP',
            align: 'start',
            value: 'customer.tpc',
            width: '10%'
        },
        {
            text: 'Account name',
            align: 'start',
            value: 'customer.name',
        },
   

        {
            text: 'Deposits',
            value: 'count',
            class: ['font-weight-bold'],
            component: {
                is: () => import('./../../components/items/fields/Link.vue'),
                bind: (_, item) => ({
                    path: '/deposit',
                    disabled: (() => item.count == 0)(),
                    query: {
                        type: (vm) => vm.type || ['ftd','rtn'],
                        customer: (_, item) => item.customer._id,
                        'sortBy[]': ['created_on'],
                        created_on: __ => __.dates.join('|'),
                        limit: (_, item) => item.count
                    }
                })
            }
        },
        { text: 'Total', value: 'amount', format: 'money' },
    ]
}