<template>
  <v-card flat color="transparent" class="ma-0 pa-0">
    <v-card-title class="d-flex flex-column align-start ma-0 pa-0 py-1"> 
      <small v-if="!$vuetify.breakpoint.mdAndUp">{{ tabNames[tab] }}</small>
      <div>{{ dateString  }}</div>
      <v-spacer></v-spacer>
    </v-card-title>
    <v-card-text class="ma-0 pa-0">
      <v-btn-toggle  >
        <v-btn  @click="today">
          <v-icon>mdi-calendar-today</v-icon>
          <span v-if="$vuetify.breakpoint.mdAndUp">Today</span> 
        </v-btn>
        <v-btn  @click="week" >
          <v-icon>mdi-calendar-week</v-icon>
          <span v-if="$vuetify.breakpoint.mdAndUp">Last 7 days</span> 
          
        </v-btn>
        <v-btn @click="month">
          <v-icon>mdi-calendar-month</v-icon>
          <span v-if="$vuetify.breakpoint.mdAndUp">This month</span> 
        </v-btn>
        <v-btn @click="dialog = true" >
          <v-icon>mdi-calendar-range</v-icon>
          <span v-if="$vuetify.breakpoint.mdAndUp">Select a date range</span> 
          
        </v-btn>
      </v-btn-toggle>
    </v-card-text>
    <v-dialog v-model="dialog" max-width="650">
      <v-card >
        <v-date-picker v-model="dates" @input="range" range full-width></v-date-picker>
      </v-card>
    </v-dialog>
  </v-card>
</template>
<script>
 
import utility from './../../utility'
export default {
  name: 'DatePicker',
  props: {
    value: { type: Array, default: () => utility.currentDateRange}
  },
 
  data() {
 
    return {
       dates: utility.currentDateRange,
       dialog: false,
       tab: 1,
       tabNames: [
         'Today',
         'Last 7 days',
         'This month',
         'Selected date range'
       ]
    }
   
  },
 
  computed: {
    dateString() {

      const values = Object.assign([], this.value).map(date => this.$options.filters.date(date))
   
      if (values[0] == values[1])
        return values[0]
      else 
        return values.join(' ~ ')
    }
  },
  methods: {

    today () {
      this.tab = 0
      this.$emit('input', utility.todayString)
      this.$emit('today', true)
    },
    week () {
      this.tab = 1
      this.$emit('input', utility.last7daysString)
      this.$emit('today', false)

    },
    month () {
      this.tab = 2
      this.$emit('input', utility.currentDateRange)
      this.$emit('today', false)
    },
    range () {
      this.tab = 3
      if (this.dates.length > 1) {
        this.dialog = false
        this.$emit('input', this.dates)
        this.$emit('today', false)
      }
    },
  }
  
  
}
</script>
 