export default {
    expand:['affiliate', 'user'],
    search: {
        customer: ['customer.name', 'customer.tpc'],
        affiliate: ['affiliate.name'],
        user: ['displayName'],
    },
    headers: {
        customer: [
            {
                text: 'TP',
                align: 'start',
                value: 'customer.tpc',
                width: '10%'
            },
            {
                text: 'Account name',
                align: 'start',
                value: 'customer.name',
            },
            { 
                text: 'Owner conversion', 
                value: 'user.displayName',
                width: 220,
                component: () => import('./../../components/items/fields/User.vue') 

             },

            { 
                text: 'FTD\'s', 
                value: 'count', 
                class: ['font-weight-bold'],
                component: { 
                    is: () => import('./../../components/items/fields/Link.vue'),
                    bind: (_, item) => ({
                        path: '/deposit',
                        disabled: (() => item.count == 0)(),
                        query: {
                            type: 'ftd',
                            customer: (_, item) => item.customer._id,
                            'sortBy[]': ['created_on'],
                            created_on: __ => __.dates.join('|'),
                            limit: (_, item) => item.count
                        }
                    })
                }  
            },
            { text: 'FTD.A', value: 'amount', format: 'money' },
            { 
                text: 'RTN\'s', 
                value: 'deposits.count',
                component: {
                    is: () => import('./../../components/items/fields/Link.vue'),
                    bind: (_, item) => ({
                        path: '/deposit',
                        disabled: (() => !item.deposits || (item.deposits && !item.deposits.count ))(),

                        query: {
                            type: 'rtn',
                            customer: (_, item) => item.customer._id,
                            created_on: vm => vm.dates.join('|'),
                            'sortBy[]': ['created_on'],
                            limit: (_, item) => item.deposits.count
                        }
                    })
                } 
            },
            { text: 'RTN.A', value: 'deposits.amount', format: 'money' },
            { text: 'Total', value: 'total', format: 'money' },
            { text: 'Date', value: 'created_on', format:'date' }
        ],
        affiliate: [

            {
                text: 'Affiliate',
                align: 'start',
                value: 'affiliate.name',
            },
            { text: 'FTD\'s', value: 'count',
                component: {
                    is: () => import('./../../components/items/fields/Link.vue'),
                    bind: (_, item) => ({
                        path: '/deposit',
                        disabled: (() => !item.count )(),
                        query: {
                            type: 'ftd',
                            affiliate: (_, item) => item.affiliate._id,
                            created_on: vm => vm.dates.join('|'),
                            'sortBy[]': ['created_on'],
                            limit: (_, item) => item.count
                        }
                    })
                }  
            },
            { text: 'FTD.A', value: 'amount', format: 'money' },
            {
                text: 'RTN\'s',
                value: 'deposits.count',
                component: {
                    is: () => import('./../../components/items/fields/Link.vue'),
                    bind: (_, item) => ({
                        path: '/deposit',
                        disabled: (() => !item.deposits || (item.deposits && !item.deposits.count))(),
                        query: {
                            type: 'rtn',
                            affiliate: (_, item) => item.affiliate._id,
                            created_on: vm => vm.dates.join('|'),
                            'sortBy[]': ['created_on'],
                            limit: (_, item) => item.deposits.count
                        }
                    })
                }
            },
            { text: 'RTN.A', value: 'deposits.amount', format: 'money' },
            { text: 'Total', value: 'total', format: 'money' },
        ],
        user: [
            {
                text: 'Agent',
                align: 'start',
                value: 'displayName',
                component: () => import('./../../components/items/fields/User.vue')
            },
            {
            text: 'FTD\'s', value: 'ftd.count', class: ['font-weight-bold']
        
          
            },
          
            { text: 'FTD.A', value: 'ftd.amount', format: 'money' },
            {
                text: 'RTN\'s', value: 'rtn.count',

                component: {
                    is: () => import('./../../components/items/fields/Link.vue'),
                    bind: (_, item) => ({
                        disabled: (() => !item.rtn || (item.rtn && !item.rtn.count))(),

                        path: '/deposit',
                        query: {
                            user: (_, item) => item._id,
                            created_on: vm => vm.dates.join('|'),

                            'sortBy[]': ['created_on'],
                            limit: (_, item) => item.rtn.count
                        }
                    })
                }
            },

            { text: 'RTN.A', value: 'rtn.amount', format: 'money' },
        ],
        user_customer: [
            {
                text: 'TP',
                align: 'start',
                value: 'tpc',
                width: '10%'
            },
            {
                text: 'Account name',
                align: 'start',
                value: 'name',
            },
            {
                text: 'Owner conversion',
                value: 'ftd.user.displayName',
                width: 220,
                component: {
                    is: () => import('./../../components/items/fields/User.vue'),
                    bind: {
                        img: 'ftd.user.profileImage'
                    }
                }

            },
            
          
            { text: 'FTD.A', value: 'ftd.amount', format: 'money' },
            { text: 'FTD.Date', value: 'ftd.created_on', format: 'date' },
            {
                text: 'RTN\'s',
                value: 'deposits.count',
                component: {
                    is: () => import('./../../components/items/fields/Link.vue'),
                    bind: (_, item) => ({
                        path: '/deposit',
                        disabled: (() => !item.deposits || (item.deposits && !item.deposits.count))(),

                        query: {
                            type: 'rtn',
                            customer: (_, item) => item._id,
                            user: (_, item) => item.user,
                            created_on: vm => vm.dates.join('|'),
                            'sortBy[]': ['created_on'],
                            limit: (_, item) => item.deposits.count
                        }
                    })
                }
            },
            { text: 'RTN.A', value: 'deposits.amount', format: 'money' },
            { text: 'Total', value: 'total', format: 'money' },
        ],

    }
}